@import url("https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700&family=Poppins:wght@200;300;400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
body {
  font-family: "Poppins", sans-serif;
  line-height: 1.5;
}
p,
h1,
h2,
h3 {
  overflow-wrap: break-word;
}
a {
  text-decoration: none;
  color: inherit;
}
ul {
  list-style: none;
}

img,
video,
canvas,
svg {
  max-width: 100%;
}

$green-color: #6cff8d;
